<template>
  <div id="app-home">
    <!-- loading -->
    <transition
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
      @after-leave="appHomeLoaded"
    >
      <app-loading
        v-if="loading"
      ></app-loading>
    </transition>
    <!-- 搜索框 -->
    <div
      v-if="!loading"
      id="search-bar"
    >
      <img src="/img/logo_search_miji.png" alt="search-logo">
      <input
        v-model="keyWords"
        type="text"
        maxlength="30"
        placeholder="秘迹搜索"
        @keyup.enter="searchKeywords"
      >
      <mu-button
        fab small
        color="primary"
        @click="searchKeywords"
      >
        <mu-icon value="search"></mu-icon>
      </mu-button>
    </div>
    <!-- 网站列表 -->
    <mu-paper
      v-if="!loading"
      id="url-container"
      :z-depth="4"
    >
      <mu-container fluid>
        <mu-row
          v-for="(groupItem, groupIndex) of urlCollection"
          :key="groupItem.group"
          align-items="center"
          class="url-items-row"
          gutter
        >
          <mu-col
            class="url-item-col"
            span="4"
            sm="2"
          >
            <mu-chip>
              <mu-avatar
                :size="32"
                :color="groupIndex % 2 === 0 ? 'primary' : 'secondary'">
                <mu-icon
                  :value="groupItem.icon"
                ></mu-icon>
              </mu-avatar>
              {{groupItem.group}}
            </mu-chip>
          </mu-col>
          <mu-col
            v-for="item in groupItem.list"
            :key="item.name"
            class="url-item-col"
            span="4"
            sm="2"
            fill
          >
            <a
              :href="item.src"
              target="_blank"
            >
              {{item.name}}
            </a>
          </mu-col>
        </mu-row>
      </mu-container>
    </mu-paper>
    <!-- live2d -->
    <transition
      enter-active-class="animated zoomIn"
      leave-active-class="animated zoomOut"
    >
      <live-2d
        v-if="!loading && showLive2D"
      ></live-2d>
    </transition>
    <!-- snackbar -->
    <mu-snackbar
      :open="snackbar.open"
      :color="snackbar.color"
      :text-color="snackbar.textColor"
    >
      <mu-icon
        :value="snackbar.icon"
        left
      ></mu-icon>
      {{snackbar.message}}
      <mu-button
        slot="action"
        :color="snackbar.textColor"
        flat
        @click="closeSnackbar"
      >close</mu-button>
    </mu-snackbar>
  </div>
</template>

<script>
import Live2D from '@/components/Live2D.vue'
import AppLoading from '@/components/AppLoading.vue'

export default {
  name: 'app-home',

  components: {
    'live-2d': Live2D,
    'app-loading': AppLoading
  },

  data () {
    return {
      loading: true,
      showLive2D: false,
      keyWords: '',
      urlCollection: [],
      snackbar: {
        open: false,
        icon: '',
        color: '',
        textColor: '',
        message: ''
      }
    }
  },

  created () {
    this.getUrlCollection()
  },

  methods: {
    getUrlCollection () {
      this.axios.get('/mock/url-collection.json')
        .then(response => {
          this.urlCollection = response.data
          this.loading = false
        })
        .catch(error => console.log(error))
    },

    appHomeLoaded () {
      this.showLive2D = true
    },

    searchKeywords () {
      if (this.keyWords) {
        let tempKeyWords = [...this.keyWords].map((value, index) => {
          let tempValue = value
          if (tempValue === ' ') {
            tempValue = '+'
          }
          return tempValue
        }).join('')
        window.open(`https://mijisou.com/?q=${tempKeyWords}`)
      } else {
        this.openSnackbar({
          icon: 'warning',
          message: '请输入关键词后再搜索',
          color: 'warning',
          textColor: '#212121',
          timeout: 3000
        })
      }
    },

    openSnackbar (opt) {
      if (opt.timeout && !this.snackbar.open) {
        let tempTimeout = setTimeout(() => {
          this.snackbar.open = false
          clearTimeout(tempTimeout)
        }, opt.timeout)
      }
      this.snackbar = {
        open: true,
        icon: opt.icon,
        color: opt.color,
        textColor: opt.textColor,
        message: opt.message
      }
    },

    closeSnackbar () {
      this.snackbar.open = false
    }
  }
}
</script>

<style scoped>
@media screen and (max-width: 600px) {
  #app-home {
    min-height: calc(100vh - 56px);
  }
}

@media screen and (min-width: 600px) {
  #app-home {
    min-height: calc(100vh - 64px);
  }
}

#app-home {
  position: relative;
  padding: 0 16px;
  background-image: url(/img/home_background_01.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  overflow: hidden;
}

#search-bar {
  margin-top: 24px;
  font-size: 0;
  text-align: center;
}

#search-bar > * {
  display: inline-block;
  position: relative;
  vertical-align: middle;
}

#search-bar > * + * {
  margin-left: -20px;
}

#search-bar > img {
  height: 40px;
  background-color: white;
  border: 2px solid #ff4081;
  border-radius: 50%;
  z-index: 2;
}

#search-bar > input {
  padding: 0 28px;
  width: 240px;
  font-size: 14px;
  line-height: 36px;
  background-color: rgba(255, 255, 255, .75);
  border: 2px solid #2196f3;
  border-radius: 4px;
  outline: none;
  transition: border .2s;
  z-index: 1;
}

#search-bar > input:focus {
  border: 2px solid #ff4081;
}

#search-bar > button {
  z-index: 2;
}

#url-container {
  margin: 24px auto;
  padding: 16px 0;
  max-width: 750px;
  background-color: rgba(255, 255, 255, .75);
}

.url-items-row {
  text-align: center;
}

.url-items-row + .url-items-row {
  margin-top: 16px;
}

.url-item-col > a {
  display: block;
  line-height: 36px;
  transition: transform .2s;
}

.url-item-col > a:hover {
  color: #2196f3;
  transform: scale3d(1.15, 1.15, 1)
}

#live-2d.live2d-wrap {
  right: 0;
  left: unset;
}
</style>
