<template>
  <div
    id="live-2d"
    :style="styleLive2D"
  >
    <!-- live2d显示区域 -->
    <transition
      enter-active-class="animated zoomIn"
      leave-active-class="animated zoomOut live2d-wrap-absolute"
    >
      <vue-live2d
        v-show="showLive2D"
        :is-move="false"
        :model-path="modelPath"
        :width="200"
        :height="320"
        :max-scale="1"
      ></vue-live2d>
    </transition>
    <!-- 按钮组 -->
    <div id="btn-group">
      <!-- 拖放按钮 -->
      <mu-button
        id="btn-drag-live2d"
        color="secondary"
        fab small
        @mousedown="moveLive2DStart"
        @touchstart="moveLive2DStart"
      >
        <mu-icon value="drag_handle"></mu-icon>
      </mu-button>
      <!-- 显隐按钮 -->
      <mu-button
        id="btn-toggle-live2d"
        :color="showLive2D ? 'secondary' : 'primary'"
        fab small
        @click="toggleLive2D"
      >
        <mu-icon
          :value="showLive2D ? 'mood' : 'mood_bad'"
        ></mu-icon>
      </mu-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'live-2d',

  data () {
    return {
      showLive2D: true,
      modelPath: [
        {
          order: 0,
          path: '/vendor/live2d-model/tsumiki/tsumiki.model.json'
        }
      ],
      thisNode: undefined,
      parentNode: undefined,
      translateX: 0,
      translateY: 0,
      panelMinWidth: 0,
      panelMinHeight: 0,
      paramMoveLive2D: {
        mouseStartX: 0,
        mouseStartY: 0,
        touchStartX: 0,
        touchStartY: 0,
        translateXStart: 0,
        translateYStart: 0,
        panelWidth: 0,
        panelHeight: 0,
        parentWidth: 0,
        parentHeight: 0
      }
    }
  },

  mounted () {
    this.thisNode = this.$el
    this.parentNode = this.$parent.$el
    let childNodesList = Array.from(this.thisNode.childNodes)
    this.panelMinWidth = Math.min(...childNodesList.map(item => item.clientWidth))
    this.panelMinHeight = Math.min(...childNodesList.map(item => item.clientHeight))
    this.thisNode.querySelectorAll('.live2d-wrap').forEach(item => {
      item.classList.remove('live2d-wrap')
    })
  },

  computed: {
    styleLive2D () {
      return {
        transform: `translate3d(${this.translateX}px, ${this.translateY}px, 0)`,
        minWidth: `${this.panelMinWidth}px`,
        minHeight: `${this.panelMinHeight}px`
      }
    }
  },

  methods: {
    toggleLive2D () {
      this.showLive2D = !this.showLive2D
      this.$nextTick(() => {
        let thisNode = this.thisNode
        let parentNode = this.parentNode
        let distanceX = parentNode.clientWidth - thisNode.clientWidth
        let distanceY = parentNode.clientHeight - thisNode.clientHeight
        this.translateX = -Math.min(-this.translateX, distanceX)
        this.translateY = -Math.min(-this.translateY, distanceY)
      })
    },

    moveLive2DStart (event) {
      let param = this.paramMoveLive2D
      param.translateXStart = this.translateX
      param.translateYStart = this.translateY
      let eventType = []
      switch (event.type) {
        case 'mousedown':
          param.mouseStartX = event.clientX
          param.mouseStartY = event.clientY
          eventType = ['mousemove', 'mouseup']
          break

        case 'touchstart': {
          param.touchStartX = event.changedTouches[0].clientX
          param.touchStartY = event.changedTouches[0].clientY
          eventType = ['touchmove', 'touchend']
          break
        }
      }
      document.addEventListener(eventType[0], this.moveLive2D)
      document.addEventListener(eventType[1], this.moveLive2DEnd)
    },

    moveLive2D (event) {
      let param = this.paramMoveLive2D
      param.panelWidth = this.thisNode.clientWidth
      param.panelHeight = this.thisNode.clientHeight
      param.parentWidth = this.parentNode.clientWidth
      param.parentHeight = this.parentNode.clientHeight
      let tempX = 0
      let tempY = 0
      let tempEvent = {}
      let tempStart = []
      switch (event.type) {
        case 'mousemove':
          tempEvent = event
          tempStart = {
            X: param.mouseStartX,
            Y: param.mouseStartY
          }
          break

        case 'touchmove':
          tempEvent = event.changedTouches[0]
          tempStart = {
            X: param.touchStartX,
            Y: param.touchStartY
          }
          break
      }
      tempX = param.translateXStart + (tempEvent.clientX - tempStart.X)
      tempY = param.translateYStart + (tempEvent.clientY - tempStart.Y)
      if (tempX <= 0 && -tempX < param.parentWidth - param.panelWidth) {
        this.translateX = tempX
      }
      if (tempY <= 0 && -tempY < param.parentHeight - param.panelHeight) {
        this.translateY = tempY
      }
    },

    moveLive2DEnd (event) {
      let eventType = []
      switch (event.type) {
        case 'mouseup':
          eventType = ['mousemove', 'mouseup']
          break

        case 'touchend':
          eventType = ['touchmove', 'touchend']
          break
      }
      document.removeEventListener(eventType[0], this.moveLive2D)
      document.removeEventListener(eventType[1], this.moveLive2DEnd)
    }
  }
}
</script>

<style scoped>
#live-2d {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 9999;
  transition: transform .13s;
}

.live2d-wrap-absolute {
  position: absolute;
  right: 0;
  bottom: 0;
}

#btn-group {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 8px;
  width: 56px;
}

#btn-group > button {
  display: block;
}

#btn-group > button + button {
  margin-top: 8px;
}
</style>
