<template>
  <div class="app-loading">
    <img
      class="app-loading-img"
      src="@/assets/loading.png"
      alt="loading"
    >
  </div>
</template>

<script>
export default {
  name: 'app-loading',

  mounted () {
    let parentNode = this.$parent.$el
    let parentNodePosition = window.getComputedStyle(parentNode).getPropertyValue('position')
    if (parentNodePosition === 'static' || parentNodePosition === '') {
      parentNode.style.position = 'relative'
    }
  }
}
</script>

<style scoped>
.app-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 9999;
}

.app-loading-img {
  width: 100px;
  animation: rotating 1s infinite linear;
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  } to {
    transform: rotate(360deg);
  }
}
</style>
